import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import SelectSearch from "react-select-search";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTransaksiReparasi } from "../actions/transaksiPenjualanAction";
import { finishSend, progressSend } from "../actions/LogicAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { gagal, warning } from "../Alert";
import ReactTooltip from "react-tooltip";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { getLokasi } from "../actions/lokasiAction";
import { getsalesmanList } from "../actions/salesmanAction";
import { getcustomerList } from "../actions/customerAction";
import { AxiosMasterGet, AxiosMasterPost } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { renderField, renderFieldSelect } from "../renderField";
import { getToday } from "../GlobalComponent/function";
import { formatGram, formatRp, getBon, getUserID } from "../helper";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormTransaksiReparasi = lazy(() =>
  import("./Component/FormTranskasiReparasi")
);
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    listReparasi: state.provinsi.listReparasi,
    listCustomer: state.provinsi.customer,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
    kodeBarang: state.provinsi.kodeBarang,
  };
};
class TransaksiReparasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      listPacking: [],
      kodeCustomer: "",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "qty",
          text: "Qty",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos Reparasi(Rp)",
          align: "right",
          headerAlign: "right",
          formatter: (cell, row) => {
            return parseInt(cell).toLocaleString("id-ID");
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, row.netto_disc)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      tanggal: "",
      data: "",
      field_level1: "col-lg-4 invisible",
      field_level2: "col-lg-4 invisible",
    };
  }

  deleteRow(rowIndex, dihapus) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array =
          JSON.parse(localStorage.getItem("transaksiReparasi")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("transaksiReparasi", JSON.stringify(array));
        var awal = localStorage.getItem("GrandTotalNetto");
        var hasil = parseFloat(awal) - parseFloat(dihapus);
        localStorage.setItem("GrandTotalNetto", parseFloat(hasil).toFixed(3));
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        }).then(() => this.props.dispatch(getListTransaksiReparasi()));
      }
    });
  }
  componentDidMount() {
    // localStorage.removeItem("transaksiReparasi");
    localStorage.removeItem("transaksiReparasi_level2");
    localStorage.removeItem("transaksiReparasi_level3");
    localStorage.removeItem("nota_reparasi_level1");
    localStorage.removeItem("nota_reparasi_level2");
    localStorage.removeItem("nota_reparasi_level3");
    localStorage.removeItem("transaksiReparasi");
    this.props.dispatch(getListTransaksiReparasi());
    this.props.dispatch(getLokasi());
    this.props.dispatch(getsalesmanList());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.setState({
      noBon: "AS/REP-" + getBon,
    })
  }

  async handleSubmit(data) {
    let ongkos_reparasi_2 = 0;
    let ongkos_reparasi_3 = 0;
    let customer_2 =
      this.state.level_customer === "LEVEL2"
        ? this.state.kodeCustomer
        : localStorage.getItem("customer_2");
    let customer_3 =
      this.state.level_customer === "LEVEL3" ? this.state.kodeCustomer : "-";
    await AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer_2}&${data.kodeBarang}`
    )
      .then((res) => (ongkos_reparasi_2 = res.data.ongkos_reparasi || 0))
      .catch(() => (ongkos_reparasi_2 = 0));
    await AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer_3}&${data.kodeBarang}`
    )
      .then((res) => (ongkos_reparasi_3 = res.data.ongkos_reparasi || 0))
      .catch(() => (ongkos_reparasi_3 = 0));
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kodeBarang}`)
      .then((res) => {
        let kode_kategori = data.kodeKategori;
        var simpan_nota_1 =
          JSON.parse(localStorage.getItem("nota_reparasi_level1")) || [];
        var simpan_nota_2 =
          JSON.parse(localStorage.getItem("nota_reparasi_level2")) || [];
        var simpan_nota_3 =
          JSON.parse(localStorage.getItem("nota_reparasi_level3")) || [];
        var posisi = simpan_nota_1.findIndex(
          (data) => data.kode_kategori === kode_kategori
        );
        var posisi_2 = simpan_nota_2.findIndex(
          (data) => data.kode_kategori === kode_kategori
        );
        var posisi_3 = simpan_nota_3.findIndex(
          (data) => data.kode_kategori === kode_kategori
        );
        if (posisi !== -1) {
          var nota_1 = {
            ongkos_rp:
              parseFloat(data.rp === undefined ? 0 : data.rp) *
                parseFloat(data.qty) +
              parseFloat(simpan_nota_1[posisi].ongkos_rp), //ongkos
            bruto: (
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_1[posisi].bruto)
            ).toFixed(3), //berat
            kode_kategori: data.kodeBarang,
            qty: data.qty,
          };
          var nota_2 = {
            ongkos_rp:
              parseFloat(ongkos_reparasi_2) * parseFloat(data.qty) +
              parseFloat(simpan_nota_2[posisi].ongkos_rp), //ongkos
            bruto: (
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_2[posisi].bruto)
            ).toFixed(3), //berat
            kode_kategori: data.kodeBarang,
            qty: data.qty,
          };
          var nota_3 = {
            ongkos_rp:
              parseFloat(ongkos_reparasi_3) * parseFloat(data.qty) +
              parseFloat(simpan_nota_3[posisi].ongkos_rp), //ongkos
            bruto: (
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_3[posisi].bruto)
            ).toFixed(3), //berat
            kode_kategori: data.kodeBarang,
            qty: data.qty,
          };
          simpan_nota_1.push(nota_1);
          simpan_nota_2.push(nota_2);
          simpan_nota_3.push(nota_3);
          simpan_nota_1.splice(posisi, 1);
          simpan_nota_2.splice(posisi_2, 1);
          simpan_nota_3.splice(posisi_3, 1);
          localStorage.setItem(
            "nota_reparasi_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_reparasi_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_reparasi_level3",
            JSON.stringify(simpan_nota_3)
          );
        } else {
          var nota_1_else = {
            ongkos_rp:
              parseFloat(data.rp === undefined ? 0 : data.rp) *
              parseFloat(data.qty), //ongkos
            bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
            kode_kategori: data.kodeBarang,
            qty: data.qty,
          };
          var nota_2_else = {
            ongkos_rp:
              parseFloat(
                ongkos_reparasi_2 === undefined ? 0 : ongkos_reparasi_2
              ) * parseFloat(data.qty), //ongkos
            bruto: parseFloat(data.bruto || 0), //berat
            kode_kategori: data.kodeBarang,
            qty: data.qty,
          };
          var nota_3_else = {
            ongkos_rp:
              parseFloat(
                ongkos_reparasi_3 === undefined ? 0 : ongkos_reparasi_3
              ) * parseFloat(data.qty), //ongkos
            bruto: parseFloat(data.bruto || 0), //berat
            kode_kategori: data.kodeBarang,
            qty: data.qty,
          };
          simpan_nota_1.push(nota_1_else);
          simpan_nota_2.push(nota_2_else);
          simpan_nota_3.push(nota_3_else);
          localStorage.setItem(
            "nota_reparasi_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_reparasi_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_reparasi_level3",
            JSON.stringify(simpan_nota_3)
          );
        }
        var simpan = {
          qty: data.qty,
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
          kode_jenis: data.kodeBarang,
        };
        var simpan_level2 = {
          qty: data.qty,
          ongkos_rp: parseFloat(
            ongkos_reparasi_2 === undefined ? 0 : ongkos_reparasi_2
          ), //ongkos
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
          kode_jenis: data.kodeBarang,
        };
        var simpan_level3 = {
          qty: data.qty,
          ongkos_rp: parseFloat(
            ongkos_reparasi_3 === undefined ? 0 : ongkos_reparasi_3
          ), //ongkos
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
          kode_jenis: data.kodeBarang,
        };

        var array = JSON.parse(localStorage.getItem("transaksiReparasi")) || [];
        var array_level2 =
          JSON.parse(localStorage.getItem("transaksiReparasi_level2")) || [];
        var array_level3 =
          JSON.parse(localStorage.getItem("transaksiReparasi_level3")) || [];
        array.push(simpan);
        array_level2.push(simpan_level2);
        array_level3.push(simpan_level3);
        localStorage.setItem("transaksiReparasi", JSON.stringify(array));
        localStorage.setItem(
          "transaksiReparasi_level2",
          JSON.stringify(array_level2)
        );
        localStorage.setItem(
          "transaksiReparasi_level3",
          JSON.stringify(array_level3)
        );
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(getListTransaksiReparasi());
        this.props.dispatch(reset("FormTransaksiReparasi"));
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }

  transaksiPenjualanSubmit() {
    if (localStorage.getItem("transaksiReparasi") === null) {
      warning("Barang masih kosong, Silahkan tambah barang");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon isi Nomor bon");
      return false;
    } else if (this.state.lokasi === "") {
      warning("Mohon pilih lokasi");
      return false;
    } else if (this.state.sales === "") {
      warning("Mohon pilih Sales");
      return false;
    }
    var simpan = {};
    switch (this.state.level_customer) {
      case "LEVEL1":
        simpan = {
          no_bon: this.state.noBon.toUpperCase(),
          no_reparasi: this.props.noFaktur.toUpperCase(),
          tanggal: this.state.tanggal || getToday(),
          kode_lokasi: this.state.lokasi.toUpperCase(),
          kode_sales: this.state.sales.toUpperCase(),
          kode_customer: this.state.kodeCustomer.toUpperCase(),
          level_customer_trx: this.state.level_customer,
          kode_customer_level2: localStorage.getItem("customer_1") || "-",
          kode_customer_level3: localStorage.getItem("customer_2") || "-",
          detail_barang: JSON.parse(localStorage.getItem("transaksiReparasi")),
        };
        break;
      case "LEVEL2":
        simpan = {
          no_bon: this.state.noBon.toUpperCase(),
          no_reparasi: this.props.noFaktur.toUpperCase(),
          tanggal: this.state.tanggal || getToday(),
          kode_lokasi: this.state.lokasi.toUpperCase(),
          kode_sales: this.state.sales.toUpperCase(),
          kode_customer: localStorage.getItem("customer_1") || "-",
          level_customer_trx: this.state.level_customer,
          kode_customer_level2: this.state.kodeCustomer,
          kode_customer_level3: localStorage.getItem("customer_2") || "-",
          detail_barang: JSON.parse(localStorage.getItem("transaksiReparasi")),
        };
        break;
      case "LEVEL3":
        simpan = {
          no_bon: this.state.noBon.toUpperCase(),
          no_reparasi: this.props.noFaktur.toUpperCase(),
          tanggal: this.state.tanggal || getToday(),
          kode_lokasi: this.state.lokasi.toUpperCase(),
          kode_sales: this.state.sales.toUpperCase(),
          kode_customer: localStorage.getItem("customer_1") || "-",
          level_customer_trx: this.state.level_customer,
          kode_customer_level2: localStorage.getItem("customer_2") || "-",
          kode_customer_level3: this.state.kodeCustomer,
          detail_barang: JSON.parse(localStorage.getItem("transaksiReparasi")),
        };
        break;
      default:
        warning("Ada Kesalah Saat Proses Level Customer");
        break;
    }
    this.props.dispatch(progressSend());
    AxiosMasterPost("reparasi/post/simpan-transaksi", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => {
            switch (this.state.level_customer) {
              case "LEVEL1":
                this.printNota("LEVEL1");
                break;
              case "LEVEL2":
                this.printNota("LEVEL1");
                this.printNota("LEVEL2");
                break;
              case "LEVEL3":
                this.printNota("LEVEL1");
                this.printNota("LEVEL2");
                this.printNota("LEVEL3");
                break;
              default:
                break;
            }
          })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("transaksiReparasi"))
          .then(() => localStorage.removeItem("transaksiReparasi_level2"))
          .then(() => localStorage.removeItem("transaksiReparasi_level3"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListTransaksiReparasi()))
          .then(() =>
            this.setState({
              noBon: "",
              namaCustomer: "",
              lokasi: "",
              sales: "",
              namaToko: "",
              tanggal: "",
              toko: "",
            })
          )
          .then(() => document.getElementById("myForm").reset())
          .then(() => window.location.reload())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: `${err}`,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }

  printNota(level) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = [];
    var footer = [];
    var columTable = [];
    switch (level) {
      case "LEVEL1":
        table = JSON.parse(localStorage.getItem("nota_reparasi_level1"));
        // for each ticket pass all its data into an array
        table.forEach((ticket, i) => {
          const ticketData = [
            ++i,
            ticket.kode_kategori,
            {content : ticket.qty, styles: { halign: "right" }},
            {content : formatGram(parseFloat(ticket.bruto)), styles: { halign: "right" }},
            {content : formatRp(ticket.ongkos_rp), styles: { halign: "right" }},
          ];
          tableRows.push(ticketData);
          // push each tickcet's info into a row
        });
        footer = [
          "",
          "",
         {content :  table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0), styles : { halign: "right" }},
          {content : formatGram(table
            .map((list) => parseFloat(list.bruto))
            .reduce((a, b) => a + b, 0)), styles : { halign: "right" }},
         {content : formatRp( table
          .map((list) => parseInt(list.ongkos_rp))
          .reduce((a, b) => a + b, 0)), styles : { halign: "right" }},
        ];
        // tableRows.push(footer);
        console.log(JSON.stringify(tableRows));
        columTable = [
          ["No",
          "Kode Kategori",
          {content : "Qty", styles: { halign: "right" }},
          {content : "Bruto \n(Gr)", styles: { halign: "right" }},
          {content : "Ongkos Reparasi", styles: { halign: "right" }},]
        ];
        // AKHIR INISIALISASI AUTOTABLE
        PrintNota(
          "No Bon",
          this.state.noBon,
          "Tanggal",
          this.state.tanggal || getToday(),
          "Sales",
          this.state.sales,
          "Customer",
          this.state.namaToko,
          getUserID(),
          getToday(),
          "",
          columTable,
          "Transaksi Reparasi",
          tableRows,
          footer
        );
        break;
      case "LEVEL2":
        table = JSON.parse(localStorage.getItem("nota_reparasi_level2"));
        // for each ticket pass all its data into an array
        table.forEach((ticket, i) => {
          const ticketData = [
            ++i,
            ticket.kode_kategori,
            ticket.qty,
            parseFloat(ticket.bruto).toFixed(3),
            ticket.ongkos_rp.toLocaleString("id-ID"),
          ];
          tableRows.push(ticketData);
          // push each tickcet's info into a row
        });
        footer = [
          "",
          "",
          table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),

          table
            .map((list) => parseFloat(list.bruto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table
            .map((list) => parseInt(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
            .toLocaleString("id-ID"),
        ];
        tableRows.push(footer);
        console.log(JSON.stringify(tableRows));
        columTable = [
         [ "No",
          "Kode Kategori",
          {content : "Qty", styles: { halign: "right" }},
          {content : "Bruto", styles: { halign: "right" }},
          {content : "Ongkos Reparasi", styles: { halign: "right" }},]
        ];
        // AKHIR INISIALISASI AUTOTABLE
        PrintNota(
          "No Bon",
          this.state.noBon,
          "Tanggal",
          this.state.tanggal || getToday(),
          "Sales",
          this.state.sales,
          "Customer",
          this.state.namaToko,
          "ADMIN",
          this.state.tanggal || getToday(),
          "",
          columTable,
          "Transaksi Reparasi",
          tableRows
        );
        break;
      case "LEVEL3":
        table = JSON.parse(localStorage.getItem("nota_reparasi_level3"));
        // for each ticket pass all its data into an array
        table.forEach((ticket, i) => {
          const ticketData = [
            ++i,
            ticket.kode_kategori,
            ticket.qty,
            parseFloat(ticket.bruto).toFixed(3),
            ticket.ongkos_rp.toLocaleString("id-ID"),
          ];
          tableRows.push(ticketData);
          // push each tickcet's info into a row
        });
        footer = [
          "",
          "",
          table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),

          table
            .map((list) => parseFloat(list.bruto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table
            .map((list) => parseInt(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
            .toLocaleString("id-ID"),
        ];
        tableRows.push(footer);
        console.log(JSON.stringify(tableRows));
        columTable = [
          [ "No",
          "Kode Kategori",
          {content : "Qty", styles: { halign: "right" }},
          {content : "Bruto", styles: { halign: "right" }},
          {content : "Ongkos Reparasi", styles: { halign: "right" }},]
        ];
        // AKHIR INISIALISASI AUTOTABLE
        PrintNota(
          "No Bon",
          this.state.noBon,
          "Tanggal",
          this.state.tanggal || getToday(),
          "",
          "",
          "Customer",
          this.state.namaToko,
          "ADMIN",
          this.state.tanggal || getToday(),
          "",
          columTable,
          "Transaksi Reparasi",
          tableRows
        );
        break;
      default:
        warning("Error Print");
        break;
    }
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data) {
    let param = data.split("-")[0];
    let namaToko = data.split("-")[1];
    let hasil = this.props.listCustomer.filter(
      (data) => data.kode_customer === param
    );
    this.setState({
      kodeCustomer: param,
      level_customer: hasil[0].level_customer,
      namaToko: namaToko
    });
    this.props.change("level_customer", hasil[0].level_customer);
    if (hasil[0].level_customer === "LEVEL2") {
      this.setState({
        field_level1: "col-lg-4",
        field_level2: "col-lg-4 invisible",
      });
    } else if (hasil[0].level_customer === "LEVEL3") {
      this.setState({
        field_level1: "col-lg-4",
        field_level2: "col-lg-4",
      });
    } else {
      this.setState({
        field_level1: "col-lg-4 invisible",
        field_level2: "col-lg-4 invisible",
      });
    }
    AxiosMasterGet(`customer/get/by-kode-customer/${param}`).then((res) => {
      let levelnya = res.data;
      this.setState({
        kode_customer_level1: levelnya.kode_customer_level1,
        kode_customer_level2: levelnya.kode_customer_level2,
      });
      this.props.change("level_customer_1", levelnya.kode_customer_level1);
      this.props.change("level_customer_2", levelnya.kode_customer_level2);
    });
  }

  render() {
    let lokasi = [
      {
        value: "SALES",
        name: "SALES",
      },
    ];
    this.props.listLokasi.map((row) => {
      let data = {
        value: row.kode_lokasi,
        name: row.nama_lokasi,
      };
      lokasi.push(data);
      return true;
    });
    return (
      <div className="container-fluid">
        {String(this.props.listPacking) !== "" ? (
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Transaksi Reparasi" />
              <div className="card-body">
                <div className="row">
                  <div className="col-lg-12">
                    <form
                      id="myForm"
                      onKeyDown={(event) => {
                        return event.key !== "Enter";
                      }}
                    >
                      <div className="row">
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Tanggal</label>
                            <input
                              name="tanggal"
                              type="date"
                              label="Tanggal"
                              className="form-control"
                              onChange={this.inputChange("tanggal")}
                              defaultValue={getToday()}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4 d-none">
                          <div className="form-group">
                            <label htmlFor="">No Bon</label>
                            <input
                              name="noBon"
                              type="text"
                              label="No Bon"
                              className="form-control"
                              onChange={this.inputChange("noBon")}
                              value={this.state.noBon}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Lokasi</label>
                            <SelectSearch
                              options={lokasi}
                              search
                              placeholder="Silahkan Pilih"
                              onChange={(data) => this.onSubmit(data, "lokasi")}
                              value={this.state.lokasi}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Sales</label>
                            <SelectSearch
                              options={this.props.listSales.map((obj) => {
                                var payload = {
                                  value: obj.kode_sales,
                                  name: obj.nama_sales,
                                };
                                return payload;
                              })}
                              search
                              placeholder="Silahkan Pilih"
                              onChange={(data) => this.onSubmit(data, "sales")}
                              value={this.state.sales}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Nama Toko</label>
                            <SelectSearch
                              options={this.props.listCustomer.map((obj) => {
                                var payload = {
                                  value: obj.kode_customer + "-" + obj.nama_customer,
                                  name:
                                    obj.nama_toko + " - " + obj.nama_customer,
                                };
                                return payload;
                              })}
                              search
                              placeholder="Silahkan Pilih"
                              onChange={(data) => this.onSubmitToko(data)}
                              onBlur={(data) => this.onSubmit(data.split("-")[1], "namaToko")}
                              value={this.state.toko}
                            />
                          </div>
                        </div>

                        <div className="col-lg-4">
                          <Field
                            name="level_customer"
                            component={renderField}
                            type="text"
                            label="Level Customer"
                            placeholder="Masukan Level Customer"
                            readOnly
                          />
                        </div>
                        <div className={this.state.field_level1}>
                          <Field
                            name="level_customer_1"
                            component={renderFieldSelect}
                            options={this.props.listCustomer
                              .filter((fil) => fil.level_customer === "LEVEL1")
                              .map((obj) => {
                                var payload = {
                                  value: obj.kode_customer,
                                  name:
                                    obj.nama_toko + " - " + obj.nama_customer,
                                };
                                return payload;
                              })}
                            type="text"
                            label="Level Customer 1"
                            placeholder="Level Customer 1"
                            onChange={(data) =>
                              localStorage.setItem("customer_1", data)
                            }
                          />
                        </div>
                        <div className={this.state.field_level2}>
                          <Field
                            name="level_customer_2"
                            component={renderFieldSelect}
                            options={this.props.listCustomer
                              .filter((fil) => fil.level_customer === "LEVEL2")
                              .map((obj) => {
                                var payload = {
                                  value: obj.kode_customer,
                                  name:
                                    obj.nama_toko + " - " + obj.nama_customer,
                                };
                                return payload;
                              })}
                            type="text"
                            label="Level Customer 2"
                            placeholder="Level Customer 2"
                            onChange={(data) =>
                              localStorage.setItem("customer_2", data)
                            }
                          />
                        </div>
                      </div>
                    </form>
                  </div>
                  <div className="col-lg-8"></div>
                  <div className="col-lg-4">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.props.dispatch(showModal())}
                        disabled={this.state.kodeCustomer === ""}
                      >
                        Tambah Barang
                        <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-12 mt-5">
                    <GlobalTabel
                      keyField="id"
                      data={this.props.listReparasi}
                      columns={this.state.columns}
                    />
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        disabled={this.props.onSend}
                        onClick={() => this.transaksiPenjualanSubmit()}
                      >
                        {this.props.onSend ? (
                          <span
                            className="spinner-border spinner-border-sm mr-3"
                            aria-hidden="true"
                          ></span>
                        ) : null}
                        Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <Skeleton width="100%" height="100%" />
        )}
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={4} />}
            >
              <FormTransaksiReparasi
                kode_customer={this.state.kodeCustomer}
                level_customer={this.state.level_customer}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </Suspense>
          }
          title="Tambah Data"
        />
      </div>
    );
  }
}

TransaksiReparasi = reduxForm({
  form: "TransaksiReparasi",
  enableReinitialize: true,
})(TransaksiReparasi);
export default connect(maptostate, null)(TransaksiReparasi);
